
.toast__mask {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.toast__body {
  position: fixed;
  z-index: 110;
  width: 10em;
  min-height: 8em;
  top: 180px;
  left: 50%;
  margin-left: -5em;
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  border-radius: 5px;
  color: #FFFFFF;
}
.toast__icon {
  margin-top: 15px;
  margin-bottom: 5px;
  width: 38px;
  height: 38px;
  vertical-align: baseline;
  display: inline-block;
}
.toast__icon_success {
  background: url(./ico-success.png) no-repeat;
  background-size: 100% 100%;
}
.toast__icon_failed {
  background: url(./ico-failed.png) no-repeat;
  background-size: 100% 100%;
}
.toast__icon_waring {
  background: url(./ico-waring.png) no-repeat;
  background-size: 100% 100%;
}
.toast__icon_loading {
  -webkit-animation: loading 1s steps(12, end) infinite;
          animation: loading 1s steps(12, end) infinite;
  background: transparent url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100% 100%;
}
.toast__content {
  margin: 0 0 15px;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes loading {
0% {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
}
100% {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}



